.co-container{
	width: 100%;
	max-width: 608px;
	min-width: 400px;
	padding: 87px 37px 96px 36px;
}

.co-columnsTitle {
	font-size: 24px;
	font-weight: 500;
	text-transform: capitalize;
}

.co-cartList{
	display: flex;
	flex-direction: column;
	gap: 14px;
	padding-top: 14px;
	padding-bottom: 22px;
}

.co-itemDetails {
	width: 250px;
	font-weight: 500;
}

.co-itemDetails > p:first-child{
	width: 100%;
	max-width: 200px;
	font-weight: 400;
	color: var(--text-grey-4);
	text-overflow: ellipsis;
  overflow: hidden; 
	white-space: nowrap;
}
.co-itemDetails > p:nth-child(2){
	padding: 0 5px;
}

.co-itemPrice {
	font-weight: 300;
}
.co-totalInfo {
	padding-bottom: 33px;
	border-bottom: 1px solid var(--text-grey-4);
	text-transform: capitalize;
}
.co-totalInfo > p:last-child{
	font-size: 24px;
	font-weight: 700;
	color: var(--button-primary);
}