.searchItem {
	width: 100%;
	height: 60px;
	padding: 10px 10px;
	list-style-type: none;
	transition: all .35s ease;
}

.searchItem:hover, .searchItem:focus {
	background-color: rgba(0, 0, 0, 0.1);
}

.searchItem > a {
	display: block;
	display: flex;
	justify-content: flex-start;
	align-items: center;
	width: 100%;
	height: 100%;
	text-decoration: none;
	color: inherit;
}

.searchItem  div {
	max-width: 60px;
	height: 100%;
}
.searchItem  div> .image {
	height: 100%;
}

.searchItem  p {
	padding-left: 10px;
	font-weight: 500;
}