.mc-c-bottomLinksWrapper{
	width: 100%;
	padding: 26px 27px 28px 27px;
	border-top: 1px solid var(--text-grey-4);
}

.mc-c-bottomLinks {
	width: 100%;
}

.mc-c-bottomLinks > a {
	display: block;
	border: 1px solid var(--text-dark-1);
	padding: 6px 30px 6px 30px;
	border-radius: 10px;
	text-decoration: none;
	font-size: 12px;
	color: var(--text-dark-1);
	transition: all .25s linear;
}

.mc-c-bottomLinks > a:hover {
	box-shadow: var( --hover-second-btn);
}